// 
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

let hasMenus = false;
// 
router.beforeEach((to, from, next) => {
  switch (to.name) {
    case 'RoleUpdate':
      if (to.query.type == 'add') {
        to.meta.title = ''
      } else {
        to.meta.title = ''
      }
      break;
    case 'AccountUpdate':
      if (to.query.type == 'add') {
        to.meta.title = ''
      } else {
        to.meta.title = ''
      }
      break;
  }
  if (to.path === '/login') {
    sessionStorage.clear();
    store.commit('loginOut');
    return next();
  } else {
    const tokenStr = sessionStorage.accessToken; //token
    if (!tokenStr) {
      // location.reload();
      return next('/login');
    }
    if (hasMenus) {
      next()
    } else {

      const requireContext = require.context('@/views/',true,/\.vue$/)
      const components = {};
      requireContext.keys().map(c=>{
        let ctor = requireContext(c).default
        components[ctor.name] = ctor
      })

      let menus = sessionStorage.roleMenu ? JSON.parse(sessionStorage.roleMenu) : []; //
      let addMenus = [];
      if (menus && menus.length > 0) { //
        menus.forEach(item => {
          if (item.children && item.children.length > 0) { //
            item.children.forEach(item2 => {
              let handleName = item2.linkUrl.slice(1);
              let componentName =  handleName[0].toUpperCase() + handleName.slice(1);
              addMenus.push({
                name: handleName[0].toUpperCase() + handleName.slice(1),
                path: item2.linkUrl,
                meta: {
                  title: item2.appellation,
                  parent: item.appellation == '' ? null : [{ title: item.appellation }]
                },
                component: components[componentName]
              });
            });
          } else {
            let handleName = item.linkUrl.slice(1);
            let componentName =  handleName[0].toUpperCase() + handleName.slice(1)
            addMenus.push({
              name: handleName[0].toUpperCase() + handleName.slice(1),
              path: item.linkUrl,
              meta: {
                title: item.appellation,
              },
              component: components[componentName]
            });
          }
        });
      }
      addMenus.map(route => {
        router.addRoute("Home", route); 
      });
      // 404
      router.addRoute({
        path: '*',
        component: ()=> import('@/views/Error404.vue')
      })
      hasMenus = true;
      next({...to,replace:true})
    }
  }
})

export default router