<template>
  <div class="groupReport">
    <el-card class="common-search-box">
      <h2 class="title"></h2>
      <div class="common-search-container">
        <div class="search-item">
          <SearchDatePicker title="" :value="groupParams.groupDateRange" @searchDateChange="searchDateChange($event)">
          </SearchDatePicker>
        </div>
        <div class="search-item">
          <el-input class="group-inp" v-model="groupParams.groupOuterId"
            @input="groupParams.groupOuterId = groupParams.groupOuterId.trim()" placeholder="id"></el-input>
          <el-input v-model="groupParams.groupKeyword" @input="groupParams.groupKeyword = groupParams.groupKeyword.trim()"
            placeholder="()"></el-input>
        </div>
        <div class="search-item search-btn">
          <el-button @click="queryGroupHandle" type="primary"></el-button>
          <el-button @click="reset"></el-button>
        </div>
      </div>
    </el-card>
    <el-card class="common-table-container">
      <div class="common-header-box">
        <h3>{{ total }}</h3>
      </div>
      <el-table ref="multipleTable" :data="tableData" border style="width: 100%" height="100%">
        <el-table-column v-for="item in tableColumn" :key="item.label" :label="item.label" :prop="item.prop"
          :min-width="item.width || 120">
        </el-table-column>
        <el-table-column label="" width="200px">
          <template slot-scope="scope">
            <el-button type="text" @click="editMemberInfo(scope.row.outerId)"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <div class="common-table-footer">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
          :current-page="groupParams.pageNum" :page-sizes="[10, 20, 30, 40]" :page-size="groupParams.pageSize"
          layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </div>
    </el-card>
    <!--  -->
    <el-dialog v-if="dialogVisible" title=" " top="20vh" :visible.sync="dialogVisible" width="70%"
      @close="closeDialog">
      <h2 class="title"></h2>
      <div class="common-search-container dialog-search">
        <div class="search-item">
          <el-input class="group-inp" v-model="dialogParams.unionId"
            @input="dialogParams.unionId = dialogParams.unionId.trim()" placeholder="ID"></el-input>
          <el-input class="group-inp" v-model="dialogParams.nickName"
            @input="dialogParams.nickName = dialogParams.nickName.trim()" placeholder="()"></el-input>
          <el-select v-model="dialogParams.status" placeholder="">
            <el-option label="" :value="0"></el-option>
            <el-option label="" :value="1"></el-option>
          </el-select>
        </div>
        <div class="search-item search-btn">
          <el-button @click="dialogQueryHandle" type="primary"></el-button>
          <el-button @click="dialogReset"></el-button>
        </div>
      </div>
      <el-table ref="dialogTable" :data="dialogTableData" border style="width: 100%" height="100%">
        <el-table-column v-for="item in dialogTableColumn" :key="item.label" :label="item.label" :prop="item.prop"
          :min-width="item.width || 120">
          <template slot-scope="scope">
            <div v-if="item.prop == 'status'">{{ numToMap[scope.row.status] }}</div>
            <div v-else>{{ scope.row[item.prop] }}</div>
          </template>
        </el-table-column>
        <el-table-column label="" width="200px">
          <template slot-scope="scope">
            <el-popconfirm confirm-button-text='' cancel-button-text='' icon="el-icon-info" icon-color="red"
              @confirm="kickMember(scope.row.id)" title="">
              <el-button slot="reference" size="small" type="primary"></el-button>
            </el-popconfirm>
            <!-- <el-button size="small" type="primary" @click="kickMember(scope.row.id)"></el-button> -->
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination-container">
        <el-pagination @size-change="handleSizeChangeDialog" @current-change="handleSizeChangeDialog"
          :current-page="dialogParams.pageNum" :page-sizes="[10, 20, 30, 40]" :page-size="dialogParams.pageSize"
          layout="total, sizes, prev, pager, next, jumper" :total="dialogTotal">
        </el-pagination>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// import utils from "@/api/utils";
export default {
  name: "GroupReport",
  data() {
    return {
      groupParams: {
        groupDateRange: "",
        groupOuterId: null,
        groupKeyword: "",
        pageSize: 10,
        pageNum: 1,
      }, //
      dialogParams: {
        unionId: "",
        nickName: "",
        status: null,
        pageSize: 10,
        pageNum: 1,
      }, //
      dialogVisible: false, //
      dialogGroupId: "", //ID
      tableData: [], //
      dialogTableData: [], //
      total: 0, //
      dialogTotal: 0, //
      //
      numToMap: {
        "0": '',
        "1": ''
      },
      //
      tableColumn: [
        { prop: "outerId", label: "ID", width: 150 },
        { prop: "outerName", label: "", width: 200 },
        { prop: "redEnvelopeSendStatistics", label: "" },
        { prop: "redEnvelopeSendMoneyStatistics", label: "" },
        { prop: "redEnvelopeSendPeopleStatistics", label: "" },
        { prop: "memberNum", label: "" },
        { prop: "onlinePeopleStatistics", label: "" },
        { prop: "updateTime", label: "", width: 200 },
        { prop: "updateTime", label: "", width: 200 }
      ],
      //
      dialogTableColumn: [
        { prop: "unionId", label: "ID" },
        { prop: "nickName", label: "" },
        { prop: "mobile", label: "" },
        { prop: "invitationUnionId", label: "ID" },
        { prop: "invitationNickName", label: "" },
        { prop: "status", label: "" },
        { prop: "onlineTime", label: "", width: 200 },
      ],
    };
  },
  created() {
    // this.query();
    this.getGroupData(); //
  },
  methods: {
    // 
    searchDateChange(val) {
      this.groupParams.groupDateRange = val;
    },
    // 
    dialogSearchDateChange(val) {
      this.dialogParams.dateRange = val;
    },
    // 
    async kickMember(id) {
      let res = await this.$Axios.delete("im/group/user/" + id);
      let { code } = res.data;
      if (code === "000000") {
        this.$message.success('!');
        this.getMemberData(this.dialogGroupId); //
        this.getGroupData(); //
      }
    },
    // 
    async getGroupData() {
      let res = await this.$Axios.post("im/group/page", JSON.stringify({
        pageSize: this.groupParams.pageSize,
        pageNum: this.groupParams.pageNum,
        dateRange: this.groupParams.groupDateRange ? `${this.groupParams.groupDateRange[0]}~${this.groupParams.groupDateRange[1]}` : null,
        levelId: null,
        groupOuterId: this.groupParams.groupOuterId ? this.groupParams.groupOuterId : null,
        groupKeyword: this.groupParams.groupKeyword
      }), { contentType: 'application/json' });
      let { code, data } = res.data;
      if (code === "000000") {
        this.total = data.total;
        this.tableData = data.records;
      }
    },
    // 
    queryGroupHandle() {
      if ((this.total / this.groupParams.pageSize) < this.groupParams.pageNum) { //
        this.groupParams.pageNum = 1;
      }
      this.getGroupData(); //
    },
    // 
    reset() {
      this.groupParams.groupOuterId = null;
      this.groupParams.groupDateRange = "";
      this.groupParams.groupKeyword = "";
      this.queryGroupHandle(); //
    },
    // 
    dialogQueryHandle() {
      this.getMemberData(this.dialogGroupId);
    },
    // 
    dialogReset() {
      this.dialogParams.unionId = "";
      this.dialogParams.nickName = "";
      this.dialogParams.status = null;
      this.getMemberData(this.dialogGroupId);
    },
    // 
    async getMemberData(id) {
      let res = await this.$Axios.post("im/group/user-list", JSON.stringify({
        pageSize: this.dialogParams.pageSize,
        pageNum: this.dialogParams.pageNum,
        groupOuterId: id,
        unionId: this.dialogParams.unionId,
        nickName: this.dialogParams.nickName,
        status: this.dialogParams.status
      }), { contentType: 'application/json' })
      let { code, data } = res.data;
      if (code === "000000") {
        this.dialogTableData = data.records;
        this.dialogTotal = data.total;
      }
    },
    // 
    editMemberInfo(id) {
      this.dialogGroupId = id;
      this.getMemberData(this.dialogGroupId);
      this.dialogVisible = true;
      // let { nickName, headUrl, id } = row;
      // this.editForm.nickName = nickName;
      // this.editForm.headUrl = headUrl;
      // this.editForm.id = id;

    },
    // 
    closeDialog() {
      this.dialogParams = {
        unionId: "",
        nickName: "",
        status: null,
        pageSize: 10,
        pageNum: 1,
      }
    },
    // 
    handleCurrentChange(val) {
      this.groupParams.pageNum = val;
      this.getGroupData();
    },
    // 
    handleSizeChange(val) {
      this.groupParams.pageSize = val;
      this.getGroupData();
    },
    // 
    handleCurrentChangeDialog(val) {
      this.dialogParams.pageNum = val;
      this.getMemberData(this.dialogGroupId);
    },
    // 
    handleSizeChangeDialog(val) {
      this.dialogParams.pageSize = val;
      this.getMemberData(this.dialogGroupId);
    }
  },
  computed: {},
};
</script>

<style lang="less" scoped>
.groupReport {

  // height: 100%;
  .title {
    padding-bottom: 10px;
    margin-bottom: 20px;
    border-bottom: 1px solid #ccc;
    color: rgb(129, 129, 129);
    font-size: 16px;
    font-weight: normal;
  }

  h3 {
    padding: 15px 5px;
    width: 100%;
    color: #444;
    background-color: #f2f2f2;
  }

  .el-form-item {
    margin-bottom: 5px;
  }

  .group-inp {
    width: 160px;
    margin-right: 15px;
  }

  /deep/ .el-form-item__label {
    display: block;
    width: 100%;
    text-align: left;
  }

  /deep/ .el-dialog {
    position: relative;
    margin-top: 10vh !important;
    min-height: 500px;

    .pagination-container {
      position: absolute;
      right: 40px;
      bottom: 40px;
    }

    .dialog-search {
      margin-bottom: 20px;
    }
  }

  /deep/ .el-dialog__header {
    padding: 10px 20px !important;
    background-color: #f2f2f2;

    .el-dialog__title {
      font-size: 14px;
    }
  }

  /deep/ .el-dialog__body {
    padding-bottom: 90px;
  }

  // .dialog-footer {
  //   .el-button {
  //     width: 100%;
  //   }
  // }
}
</style>