<template>
  <div id="editor"></div>
</template>

<script>
import E from "wangeditor";
export default {
  props: {
    content: {
      type: String,
      default: "",
    },

    // 
    imageSize: {
      type: Number,
      default: 2,
    },
    // 
    // imageNum: {
    //   type: Number,
    //   default: 5,
    // },
  },
  data() {
    return {
      editor: "",
    };
  },
  mounted() {
    this.editor = new E("#editor");

    // 
    this.editor.config.showFullScreen = false;

    // onchange,
    this.editor.config.onchange = (newHtml) => {
      console.log("changenewHtml", newHtml);
      this.$emit("changeEditor", newHtml);
    };

    // onchange200ms
    this.editor.config.onchangeTimeout = 1;

    this.editor.config.uploadImgMaxSize = this.imageSize * 1024 * 1024; // 2M

    this.editor.config.uploadImgAccept = ["jpg", "jpeg", "png", "gif", "bmp"];

    this.editor.config.uploadImgMaxLength = 1; //  1 

    // this.editor.config.uploadImgShowBase64 = true; // base 64 

    this.editor.config.uploadFileName = "file"; // 

    this.editor.config.showLinkImg = false; // 

    this.editor.config.uploadImgHeaders = {
      Authorization: sessionStorage.accessToken,
    };

    this.editor.config.uploadImgServer =
      process.env.VUE_APP_BASE_URL + "/api/rest/file"; // 

    this.editor.config.uploadImgHooks = { // 
      // 
      before: function (xhr) {
        console.log(xhr);

        // 
        return {
          prevent: false,
          msg: "",
        };
      },
      // 
      success: function (xhr) {
        console.log("success", xhr);
      },
      // 
      fail: function (xhr, editor, resData) {
        console.log("fail", resData);
      },
      //  http 
      error: function (xhr, editor, resData) {
        console.log("error", xhr, resData);
      },
      // 
      timeout: function (xhr) {
        console.log("timeout");
        this.$message.waring('')
      },
      // 
      //  { errno: 0, data: [...] }  customInsert
      customInsert: function (insertImgFn, result) {
        // result 
        console.log("customInsert", result);

        // insertImgFn  src 
        // insertImgFn(result.data[0]);
        insertImgFn(result.data.url);
      },
    };
    this.editor.config.customAlert =  (str) => {
      this.$message.error(str);
    }

    this.editor.config.menus = [
      "head",
      "bold",
      "fontSize", // 
      "fontName", // 
      "italic",
      "underline",
      "strikeThrough", // 
      "foreColor", // 
      "backColor", // 
      "link", // 
      "list", // 
      "justify", // 
      "quote", // 
      "emoticon", // 
      "image", // 
      "table", // 
      "video", // 
      "code", // 
      "undo", // 
      "redo", // 
    ];
    this.editor.create();
  },
  watch: {
    content() {
      this.editor.txt.html(this.content);
    },
  },
};
</script>

