<template>
  <div class="group-red-packet">
    <el-card class="common-search-box">
      <h2 class="title"></h2>
      <div class="common-search-container">
        <div class="search-item">
          <el-input class="item" v-model="params.groupId" @input="params.groupId = params.groupId.trim()" clearable
            placeholder="ID"></el-input>
          <el-input class="item" v-model="params.fromUnionId" @input="params.fromUnionId = params.fromUnionId.trim()"
            clearable placeholder="ID"></el-input>
          <el-input class="item" v-model="params.toUnionId" @input="params.toUnionId = params.toUnionId.trim()"
            clearable placeholder="ID"></el-input>
          <el-select v-model="params.status" placeholder="">
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="2"></el-option>
            <el-option label="" :value="3"></el-option>
            <el-option label="" :value="4"></el-option>
            <el-option label="" :value="5"></el-option>
          </el-select>
          <el-select v-model="params.secondType" placeholder="">
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="2"></el-option>
          </el-select>
          <div class="search-item date-picker">
            <el-date-picker v-model="params.createTime" value-format="yyyy-MM-dd" type="date" placeholder="">
            </el-date-picker>
          </div>
        </div>
        <div class="search-item search-btn">
          <el-button @click="getData" type="primary"></el-button>
          <el-button @click="reset"></el-button>
        </div>
      </div>
    </el-card>
    <el-card class="common-table-container">
      <div class="common-header-box">
        <h3>{{ totalAmount }}</h3>
      </div>
      <el-table ref="multipleTable" :data="tableData" border style="width: 100%" height="100%">
        <el-table-column v-for="item in tableColumn" :key="item.label" :label="item.label" :prop="item.prop"
          :min-width="item.width || 120">
          <template slot-scope="scope">
            <div v-if="item.prop === 'status'">{{ redPacketStatus[scope.row.status] }}</div>
            <div v-else-if="item.prop === 'secondType'">{{ redPacketType[scope.row.secondType] }}</div>
            <div v-else>{{ scope.row[item.prop] }}</div>
          </template>
        </el-table-column>
      </el-table>

      <div class="common-table-footer">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
          :current-page="params.pageNum" :page-sizes="[10, 20, 50, 100]" :page-size="params.pageSize"
          layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "GroupRedPacketList",
  data() {
    return {
      // 
      params: {
        groupId: null,
        fromUnionId: "",
        toUnionId: "",
        status: null,
        secondType: null,
        createTime: "",
        pageSize: 10,
        pageNum: 1,
      },
      tableData: [], //
      total: 0, //
      totalAmount: 0, //
      // 
      redPacketStatus: {
        "1": "",
        "2": "",
        "3": "",
        "4": "",
        "5": "",
      },
      redPacketType: {
        "1": "",
        "2": ""
      },
      //
      tableColumn: [
        { prop: "redEnvelopeId", label: "ID" },
        { prop: "outerName", label: "" },
        { prop: "fromUnionId", label: "ID" },
        { prop: "fromUserNickName", label: "" },
        { prop: "toUnionId", label: "ID" },
        { prop: "toUserNickName", label: "" },
        { prop: "receivedMoneyAmount", label: "" },
        { prop: "status", label: "" },
        { prop: "secondType", label: "" },
        { prop: "receiveOrRefundTime", label: "", width: 200 }
      ]
    };
  },
  created() {
    this.pageGroupRedPacket(); //
  },
  methods: {
    // 
    async pageGroupRedPacket() {
      let res = await this.$Axios.post("group-red-envelope/page", JSON.stringify({
        pageSize: this.params.pageSize,
        pageNum: this.params.pageNum,
        groupId: this.params.groupId ? this.params.groupId : null,
        fromUnionId: this.params.fromUnionId,
        toUnionId: this.params.toUnionId,
        secondType: this.params.secondType ? this.params.secondType : null,
        status: this.params.status ? this.params.status : null,
        createTime: this.params.createTime
      }), { contentType: 'application/json' });
      let { code, data } = res.data;
      if (code === "000000") {
        this.total = data.total;
        this.totalAmount = data.total != 0 ? data.records[0].totalMoneyAmount : 0;
        this.tableData = data.records;
      }
    },
    // 
    getData() {
      if ((this.total/this.params.pageSize)<this.params.pageNum) { //
        this.params.pageNum = 1;
      }
      this.pageGroupRedPacket(); 
    },
    // 
    reset() {
      this.params.fromUnionId = "";
      this.params.toUnionId = "";
      this.params.createTime = "";
      this.params.secondType = null;
      this.params.groupId = null;
      this.params.status = null;
      this.pageGroupRedPacket(); //
    },
    // 
    handleCurrentChange(val) {
      this.params.pageNum = val;
      this.pageGroupRedPacket(); //
    },
    // 
    handleSizeChange(val) {
      this.params.pageSize = val;
      this.pageGroupRedPacket(); //
    }
  }
};
</script>

