<template>
  <div class="batch-edit-pwd">
    <el-card class="common-table-container">
      <div class="common-header-box">
        <h3></h3>
      </div>
      <main>
        <el-form :model="pwdRuleForm" :rules="rules" ref="pwdRuleForm" label-width="120px" class="demo-ruleForm">
          <el-form-item label="" prop="password">
            <el-input show-password v-model="pwdRuleForm.password" clearable></el-input>
          </el-form-item>
          <el-form-item label="" prop="payPassword">
            <el-input show-password v-model="pwdRuleForm.payPassword" clearable></el-input>
          </el-form-item>
          <el-form-item label="" prop="mobiles">
            <el-input type="textarea" rows="15" v-model="pwdRuleForm.mobiles" clearable></el-input>
            <p class="tips">*  50</p>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('pwdRuleForm')"></el-button>
            <el-button @click="resetForm('pwdRuleForm')"></el-button>
          </el-form-item>
        </el-form>
      </main>
      <div class="common-table-footer">
        
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "BatchEditPwd",
  data() {
    return {
      pwdRuleForm: {
        password: "",
        payPassword: "",
        mobiles: "",
      },
      rules: {
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, message: '5', trigger: 'blur' }
        ],
        payPassword: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, message: '5', trigger: 'blur' }
        ]
      }
    };
  },
  created() {
  },
  methods: {
    // 
    sizeChange(target, val) {
      // console.log(data, e)
      if (target === "sysPersonal") {  //switch
        this.tableParams.pageNum = val;
        this.pagePersonal(1) //
      } else if (target === "userPersonal") {
        this.userPersonalParams.pageNum = val;
        this.pagePersonal(2) //
      } else if (target === "sysGroup") {
        this.sysGroupParams.pageNum = val;
        this.pageGroup(1) //
      } else {
        this.userGroupParams.pageNum = val;
        this.pageGroup(2) //
      }
    },
    currentChange(target, val) {
      // console.log(data, e)
      if (target === "sysPersonal") {
        this.sysPersonalParams.pageSize = val;
        this.pagePersonal(1) //
      } else if (target === "userPersonal") {
        this.userPersonalParams.pageSize = val;
        this.pagePersonal(2) //
      } else if (target === "sysGroup") {
        this.sysGroupParams.pageSize = val;
        this.pageGroup(1) //
      } else {
        this.userGroupParams.pageSize = val;
        this.pageGroup(2) //
      }
    },
    // 
    async batchUpdatePwd() {
      let res = await this.$Axios.post("user/batch-update-password", JSON.stringify(this.pwdRuleForm), { contentType: 'application/json' });
      // console.log(res);
      let { code } = res.data;
      if (code === "000000") {
        this.$message.success("");
      }
    },
    // 
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.batchUpdatePwd(); //
          this.$refs[formName].resetFields(); //
        }
      });
    },
    // 
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  },
};
</script>

<style lang="less" scoped>
.batch-edit-pwd {
  main {
    width: 50%;

    .tips {
      font-size: 12px;
      color: rgb(235, 4, 4);
    }
  }
}
</style>