<template>
  <div class="groupList">
    <el-card class="common-search-box">
      <div class="common-search-container">
        <div class="search-item">
          <SearchDatePicker
            title=""
            :value="params.dateRange"
            @searchDateChange="searchDateChange($event)"
          ></SearchDatePicker>
        </div>
        <div class="search-item">
          <span class="title"></span>
          <el-select v-model="params.levelId" placeholder="" clearable>
            <el-option
              v-for="item in transactionTypeList"
              :key="item.id"
              :label="item.description"
              :value="item.id"
            >
            </el-option>
          </el-select>
        </div>
        <div class="search-item">
          <span class="title"></span>
          <el-input
            v-model="params.groupKeyword"
            suffix-icon="el-icon-search"
            placeholder="ID"
            @input="params.groupKeyword = params.groupKeyword.trim()"
          ></el-input>
        </div>
        <div class="search-item search-btn">
          <el-button @click="query('query')" type="primary"></el-button>
          <el-button @click="reset"></el-button>
        </div>
      </div>
    </el-card>
    <el-card class="common-table-container">
      <div class="common-header-box">
        <h3></h3>
      </div>
      <el-table
        ref="multipleTable"
        :data="tableData"
        @select="handleSelectionChange"
        @select-all="handleSelectionChange"
        border
        height="100%"
      >
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column
          v-for="item in tableColumn"
          :key="item.label"
          :label="item.label"
          :prop="item.prop"
          :min-width="item.width || 100"
        ></el-table-column>
        <el-table-column label="" width="200px">
          <template slot-scope="scope">
            <el-button type="text" @click="viewDetails(scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>

      <div class="common-table-footer">
        <div>
          <el-checkbox
            v-model="checkAll"
            @change="checkAllChange"
            style="margin-right: 30px"
            ></el-checkbox
          >
          <el-button @click="exportList"></el-button>
          <el-button @click="exportAllList"></el-button>
        </div>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="params.pageNum"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="params.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
      </div>
    </el-card>
    <el-dialog title="" :visible.sync="dialogVisible" width="45%">
      <div class="common-search-container">
        <div class="search-item">
          <span class="title"></span>
          <el-date-picker
            v-model="detailDateRange"
            type="daterange"
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            value-format="yyyy-MM-dd"
            :picker-options="pickerOptions"
            @change="handleDetailDate"
          ></el-date-picker>
        </div>
      </div>
      <el-row :gutter="20">
        <el-col :span="12" v-for="(item, index) in groupDataList" :key="index">
          <div class="groupDataList-box">
            <h3>{{ item.title }}</h3>
            <div class="groupDataList-content">
              <el-avatar
                :size="70"
                :src="require('@/assets/img/red-packet.png')"
              ></el-avatar>
              <div>
                <h5>{{ item.subTitle }}</h5>
                <span v-if="item.numProp=='redEnvelopeSendStatistics' || item.numProp=='redEnvelopeReceiveStatistics'">{{ item.num }}</span>
                <span v-else>{{ item.num.toFixed(2) }}</span>
              </div>
            </div>
            <p>
              {{ item.totalName }} 
              <span v-if="item.numProp=='redEnvelopeSendStatistics' || item.numProp=='redEnvelopeReceiveStatistics'">{{ item.total }}</span>
              <span v-else>{{ item.total.toFixed(2) }}</span>
            </p>
          </div>
        </el-col>
      </el-row>
    </el-dialog>
  </div>
</template>

<script>
import utils from "@/api/utils";
export default {
  name: "GroupList",
  data() {
    return {
      dialogVisible: false,
      params: {
        dateRange: "",
        groupKeyword: "",
        levelId: "",
        pageNum: 1,
        pageSize: 10
      },
      params2: {
        dateRange: "",
        groupKeyword: "",
        levelId: "",
        pageNum: 1,
        pageSize: 10
      },
      detailDateRange: "",
      groupDataList: [
        {
          title: "",
          subTitle: "",
          num: 0,
          totalName: "",
          total: 0,
          numProp:'redEnvelopeSendStatistics',
          totalProp:'redEnvelopeSendHistoryStatistics',
        },
        {
          title: "",
          subTitle: "",
          num: 0,
          totalName: "",
          total: 0,
          numProp:'redEnvelopeReceiveStatistics',
          totalProp:'redEnvelopeReceiveHistoryStatistics',
        },
        {
          title: "",
          subTitle: "",
          num: 0,
          totalName: "",
          total: 0,
          numProp:'redEnvelopeSendMoneyStatistics',
          totalProp:'redEnvelopeSendMoneyHistoryStatistics',
        },
        {
          title: "",
          subTitle: "",
          num: 0,
          totalName: "",
          total: 0,
          numProp:'redEnvelopeReceiveMoneyStatistics',
          totalProp:'redEnvelopeReceiveMoneyHistoryStatistics',
        },
      ],
      transactionTypeList: [],
      tableData: [], 
      total: 0, //
      tableColumn: [
        { prop: "outerId", label: "ID" },
        { prop: "outerHeadUrl", label: "" },
        { prop: "outerName", label: "" },
        { prop: "outerOwner", label: "", width: 120 },
        { prop: "level.description", label: "", width: 120 },
        { prop: "memberNum", label: "" },
        { prop: "createTime", label: "", width: 150 },
      ],
      // 
      checkAll: false,
      // 
      selectData: [],
      tableId: '',
    };
  },
  created() {
    this.queryTransactionType();
    this.query();
  },
  methods: {
    // 
    searchDateChange(val) {
      this.params.dateRange = val;
    },
    // 
    query(type) {
      if (type == "query") {
        this.params.pageNum = 1;
        this.params2 = JSON.parse(JSON.stringify(this.params));
      } else {
        this.params2.pageNum = this.params.pageNum;
        this.params2.pageSize = this.params.pageSize;
      }
      let params = JSON.parse(
        JSON.stringify(utils.objDeleteNull(this.params2))
      );
      params.dateRange = this.params2.dateRange
        ? this.params2.dateRange.join(" ~ ")
        : null;
      this.$Axios
        .post("/im/group/page", JSON.stringify(params),{contentType: 'application/json'})
        .then((res) => {
          if( res.data.data ) {
            this.tableData = res.data.data.records;
            this.total = res.data.data.total;
          }
        })
    },
    // 
    queryTransactionType() {
      this.$Axios.get('/im/group/level-list').then( res=> {
        if( res.data.data ) {
          this.transactionTypeList = res.data.data
        }
      })
    },
    // 
    reset() {
      this.params.groupKeyword = null;
      this.params.dateRange = null;
      this.params.levelId = null;
      this.query("query");
    },
    // 
    viewDetails(row) {
      this.detailDateRange = "";
      this.getGroupDetail(row.outerId);
      this.tableId = row.id;
      this.dialogVisible = true;
    },
    // 
    getGroupDetail(groupOuterId, dateRange) {
      let params = {
        groupOuterId,
        dateRange: dateRange ? dateRange.join(" ~ ") : null
      }
      this.$Axios.post('/im/group/statistics',JSON.stringify(params), {contentType: 'application/json'})
      .then( res=> {
        if( res.data.data) {
          let data = res.data.data;
          this.groupDataList.forEach(item=>{
              item.num = data[item.numProp]
              item.total = data[item.totalProp]
          })
        }
      })
    },
    handleDetailDate(val) {
      this.getGroupDetail(this.tableId, val)
    },
    // 
    exportList() {
      if (this.selectData.length > 0) {
        let ids = utils.arrayOpera(this.selectData, "id");
        let params = {
          ids,
          groupKeyword: this.params2.groupKeyword,
          levelId: this.params2.levelId,
          dateRange: this.params2.dateRange
            ? this.params2.dateRange.join(" ~ ")
            : null,
        };
        this.$Axios
          .post("/im/group/export", JSON.stringify(params), {
            responseType: "blob", contentType: 'application/json'
          })
          .then((data) => {
            utils.excelDownload(data, ".xls");
          })
      } else {
        this.$message.warning("");
        return;
      }
    },
    exportAllList() {
      let params = {
        groupKeyword: this.params2.groupKeyword,
        levelId: this.params2.levelId,
        dateRange: this.params2.dateRange
          ? this.params2.dateRange.join(" ~ ")
          : null,
      };
      this.$Axios.post("/im/group/export", JSON.stringify(params), {
        responseType: "blob", contentType: 'application/json'
      })
      .then((data) => {
        utils.excelDownload(data, ".xls");
      })
    },
    handleCurrentChange(page) {
      this.params.pageNum = page;
      this.checkAll = false;
      this.query();
    },
    handleSizeChange(val) {
      this.params.pageSize = val;
      this.checkAll = false;
      this.query();
    },
    handleSelectionChange(val) {
      this.selectData = val;
      this.checkAll = val.length == this.tableData.length;
    },
    // 
    checkAllChange(val) {
      if (val) {
        this.$refs.multipleTable.toggleAllSelection()
      } else {
        this.$refs.multipleTable.clearSelection();
        this.selectData = [];
      }
    },
  },
  computed: {
    pickerOptions() {
      return {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
      };
    },
  }
};
</script>

<style lang="less" scoped>
.groupList {
  // height: 100%;
  .el-row {
    margin-top: 40px;
    .el-col {
      margin-bottom: 20px;
    }
  }
  .groupDataList-box {
    background-color: #f2f2f2;
    padding: 20px;
    height: 140px;
    border-radius: 4px;
    color: #a3a3a3;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    h3 {
      font-weight: normal;
      font-size: 14px;
    }
    p {
      span {
        color: #555555;
        font-weight: 650;
        margin-left: 5px;
      }
    }
    .groupDataList-content {
      display: flex;
      align-items: center;
      div {
        margin-left: 10px;
        h5 {
          font-weight: normal;
          margin-bottom: 10px;
          font-size: 12px;
        }
        span {
          color: #000000;
          font-weight: 700;
          font-size: 24px;
        }
      }
    }
  }
}
</style>